.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #533355c5;
  width:100%;
  min-height: 10vh;
  display: flex;
  flex-direction: line;
  font-size: calc(10px + 2vmin);
  color: rgb(255, 255, 255);
  padding: 15px;
  top:0;
  position:fixed;
}

.App-link {
  color: #e961fb;
}

.jbutton {
  padding: 10px;
  border: none!important;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  min-width: 20vh;
  background-color: #af12c4;
  font-size: calc(10px + 0.6vmin);
  color: rgb(255, 255, 255);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: bold;
  height: 7vh;
  transition-duration: 0.3s;
}

.jbutton:hover {
  background-color: #7810a1;
  color: rgb(216, 175, 219);
  transform: scale(1.05);
}

.App-header > .jbutton {
  margin-left: 15px;
  margin-top: 7.5px;
}

.page-content {
  margin-top: 20vh;
}

.footer-text {
  color: grey;
}

.title-heading {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  color: white;
  font-size: 8vh;
  padding:1vh;
}

.addon-pack {
  background-color: #430350;
  color: white;
  display: flex;
  flex-direction:row;
  padding:25px;
}


.addon-pack > img {
  width: 50%;
  border-radius: 15px;
}

.addon-pack-button-part {
  margin-left: 12vh;
  background-color: #430350;
  color: white;
  display: flex;
  flex-direction:column;
  padding:25px;
}


.addon-pack-button-part > button{
  margin-bottom: 3vh;
}

.popup-window {
  color: rgb(0, 0, 0);
  position:absolute;
  padding: 15px;
  background-color: whitesmoke;
  border-radius: 15px;
  transform: scale(0);
  opacity: 0;
  width: 70vh;
  height: 40vh;
  transition-duration: 0.3s;
}

.popup-window.active {
  opacity: 100%;
  transform: scale(1);
}

.close-btn > div {
  background-color: grey;
  border-radius: 10px;
  color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 3px;
}

ul {
  list-style: none;
}


.burger-menu {
  transform: scale(0);
  transition-duration: 0.3s;
}


.popup-content > a {
  word-wrap:break-word;
}

.popup-content {
  height: 35vh;
  overflow-y: scroll;
}

.popup-content > p > img {
  width: 50vh
}

.jedolsmc-pack > img {
  width: 25%;
  margin-left: 15vh;
}

.jedolsmc-pack > .addon-pack-button-part {
  margin-left: 55vh;
}


.burger-bar {
  width: 50%;
  height: 100%;
  position:fixed;
  top: 0;
  background-color: white;
  border-right: 3px solid gainsboro;
  border-radius:  0 15px 15px 0;
  visibility: hidden;
  left: -50%;
  transition-duration: 0.5s;
}

.burger-bar > * {
  margin-left: 25px;
}

.burger-bar > .title-part {
  display: flex;
  flex-direction: row;
  flex-flow: row;
  gap: 70%;
}

.burger-bar > .title-part > button {
  flex-shrink: 50%;
  transform: scale(2);
}

.burger-bar.opened {
  transform: scale(1);
  visibility: visible;
  left: 0;
}

.burger-bar > .content {
  display: flex;
  flex-direction: column;
  width: 90%;
  gap: 15px;
}



.logo-img > img {
  width: 50%;
}

.logo-img {
  border-radius: 15px;
  width: 25vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition-duration: 0.5s;
  transition-timing-function: ease-out;
  padding: 0 3vh 0 3vh;
}

.logo-img:hover {
  background-color: #dd87f348;
}

.burger-menu-desktop {
  transform: scale(2);
}

.logo-img > * {
  margin:  0 3vh 0 3vh;
}


@media screen and (min-width: 200px) and (max-width: 700px) {
  .addon-pack {
    flex-direction: column;
  }
  .addon-pack-button-part {
    margin-left: 0
  }

  .addon-pack > img {
    width: 100%;
  }

  .popup-window {
    max-width: 33vh;
  }

  .burger-menu {
    transform: scale(3);
    position: absolute;
    left: 6vh;
    top: 5.5vh
  }

  .App-header > .jbutton {
    transform: scale(0);
  }

  .App-header {
    max-height: 1vh;
  }

  .burger-menu-popup {
    position: fixed;
    top: 15vh;
    left: 5vh;
    background-color: rgba(128, 128, 128, 0.445);
  }  

  .burger-menu-popup > div > ul > li {
    margin-top: 1vh;
    margin-left: 2vh
  }

  .title-heading {
    font-size: 4vh;
  }

  .burger-menu-popup > div > ul > li > .jbutton {
    height: 3vh;
    width: 25vh;
  }

  .burger-menu-popup > div > ul  {
    position: absolute;
    right: 5.5vh
  }

  
  .popup-content > p > img {
    width: 22vh
  }

  
  .jedolsmc-pack > img {
    width: 100%;
    margin-left: 0;
  }

  .jedolsmc-pack > .addon-pack-button-part {
    margin-left: 0
  }

  .burger-menu-desktop {
    transform: scale(17);
  }
  
  .burger-bar {
    width: 100%;
    left: -100%;
  }

  .burger-bar > * {
    margin-left: 20px;
  }
  
  .burger-bar > .title-part {
    gap: 55%;
  }

  .burger-bar.opened {
    border-radius: 0;
  }
}
